import React, { useContext } from "react"
import styled from "styled-components"

import logo from "../icons/AM-logo-3.svg"

import { ThemeContext } from "../context/ThemeContext"

const Screen = styled.div`
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: ${props => (props.dark ? `#14181c` : `#f5f5f5`)};
  display: flex;
  align-items: center;
  justify-content: center;
  transform: ${props => (props.fade ? `translateY(0)` : `translateY(-100vh)`)};
  transition: transform 0.5s;
  border-bottom: 4px solid #2a78ff;
`

export default function IntroScreen({ fade }) {
  const { isDark } = useContext(ThemeContext)
  return (
    <Screen dark={isDark} fade={fade}>
      <img src={logo} alt="logo" height="auto" width="100px" />
    </Screen>
  )
}
