import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import Button from "../button"

import { ButtonsWrap, Container, ProjectContent } from "./styled-assets"

const Project = ({ dark }) => {
  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "nowLive-main.png" }) {
        childImageSharp {
          fluid(maxWidth: 900) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <Container>
      <Img
        fluid={data.placeholderImage.childImageSharp.fluid}
        imgStyle={{
          objectPosition: "top center",
          repeat: "no-repeat",
        }}
      />
      <ProjectContent dark={dark}>
        <span>nowLIVE</span>
        <p>Front-end landing page</p>
        <ButtonsWrap>
          <Button
            link="https://almir.dev/nowLIVE"
            dark={dark}
            text="Live Site"
          ></Button>
          <Button
            link="https://github.com/almirmujic/nowLIVE"
            dark={dark}
            text="Code"
          ></Button>
        </ButtonsWrap>
      </ProjectContent>
    </Container>
  )
}

export default Project
