import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import Button from "../button"

import { ButtonsWrap, Container, ProjectContent } from "./styled-assets"

const Project = ({ dark }) => {
  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "portfolio.png" }) {
        childImageSharp {
          fluid(maxWidth: 900) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <Container>
      <Img
        fluid={data.placeholderImage.childImageSharp.fluid}
        imgStyle={{
          objectPosition: "top left",
          repeat: "no-repeat",
        }}
      />
      <ProjectContent dark={dark}>
        <span>Portfolio</span>
        <p>Built using Gatsby.js</p>
        <ButtonsWrap>
          <Button
            link="https://github.com/almirmujic/almirmujic.github.io"
            dark={dark}
            text="Code"
          ></Button>
        </ButtonsWrap>
      </ProjectContent>
    </Container>
  )
}

export default Project
