import React from "react"
import styled from "styled-components"

const ButtonMode = styled.a`
  all: unset;
  padding: 0.5rem;
  font-size: 14px;
  border-radius: 9px;
  background: ${props => (props.dark ? `#333` : `#e6e6e6`)};
  color: ${props => (props.dark ? `#e6e6e6` : `#333`)};
  &:hover {
    cursor: pointer;
  }
`

export default function Button({ text, dark, link }) {
  return (
    <ButtonMode target="_blank" href={link} dark={dark}>
      {text}
    </ButtonMode>
  )
}
