import React from "react"
import styled from "styled-components"

import JSIcon from "../icons/js-square.svg"
import NodeIcon from "../icons/node.svg"
import ReactIcon from "../icons/react.svg"
import HTMLIcon from "../icons/html5.svg"
import CSSIcon from "../icons/css3-alt.svg"
import SASSIcon from "../icons/sass.svg"
import WebpackIcon from "../icons/webpack.svg"
import NpmIcon from "../icons/npm.svg"
import Restful from "../icons/restful.svg"

const Container = styled.section`
  margin-top: 8rem;
`

const Wrapper = styled.div`
  width: 90%;
  max-width: 1300px;
  margin: 0 auto;
  padding-top: calc(2rem + 8vw);
`

const SkillsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, 75px);
  grid-gap: 2rem;
`

const Heading = styled.span`
  display: block;
  font-size: 28px;
  text-align: left;
  margin-bottom: 2rem;
  @media (min-width: 768px) {
    font-size: 36px;
  }
`

const Skill = styled.div`
  display: grid;
  grid-template-rows: 50px 1fr;
  height: 75px;
  justify-items: center;
  grid-gap: 0.5rem;
  font-size: 14px;
  img {
    height: 50px;
  }
`

const devSkills = [
  {
    icon: JSIcon,
    name: "JavaScript",
  },
  {
    icon: ReactIcon,
    name: "React",
  },
  {
    icon: HTMLIcon,
    name: "HTML",
  },
  {
    icon: CSSIcon,
    name: "CSS",
  },
  {
    icon: SASSIcon,
    name: "Sass",
  },
  {
    icon: WebpackIcon,
    name: "Webpack",
  },
  {
    icon: NpmIcon,
    name: "npm",
  },
  {
    icon: NodeIcon,
    name: "Node.js",
  },
  {
    icon: Restful,
    name: "REST APIs",
  },
]

export default function skills() {
  return (
    <Container>
      <Wrapper>
        <Heading>Skills</Heading>
        <SkillsGrid>
          {devSkills.map((skill, idx) => (
            <Skill key={idx}>
              <img src={skill.icon} alt={skill.name} aria-label={skill.name} />
              <span>{skill.name}</span>
            </Skill>
          ))}
        </SkillsGrid>
      </Wrapper>
    </Container>
  )
}
