import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import Button from "../button"

import { ButtonsWrap, Container, ProjectContent } from "./styled-assets"

const Project = ({ dark }) => {
  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "spacex-desk-dark.png" }) {
        childImageSharp {
          fluid(maxWidth: 900) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <Container>
      <Img
        fluid={data.placeholderImage.childImageSharp.fluid}
        imgStyle={{
          objectPosition: "top center",
          repeat: "no-repeat",
        }}
      />
      <ProjectContent dark={dark}>
        <span>SpaceX Launches</span>
        <p>Project using SpaceX api and React library</p>
        <ButtonsWrap>
          <Button
            link="https://spacex-launches1.netlify.app/"
            dark={dark}
            text="Live site"
          />
          <Button
            link="https://github.com/almirmujic/spacex-launches"
            dark={dark}
            text="Code"
          />
        </ButtonsWrap>
      </ProjectContent>
    </Container>
  )
}

export default Project
