import React, { useContext } from "react"
import styled from "styled-components"

import logo from "../icons/AM-logo-3.svg"
import GitHub from "../icons/github-square.svg"
import Email from "../icons/paper-plane.svg"
import LinkedIn from "../icons/linkedin.svg"
import Sun from "../icons/sun.svg"
import Moon from "../icons/moon.svg"

import { ThemeContext } from "../context/ThemeContext"

const Navbar = styled.nav`
  width: 100%;
  height: auto;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  background-color: ${props => (props.dark ? "#14181cfc" : "#f5f5f5")};
  transform: translateY(-2rem);
  /* transform: translateY(-2rem); */
  /* transform: ${props =>
    props.scrollingUp ? `translateY(0)` : `translateY(-2rem)`}; */
  transition: background-color 0.5s ease;
`

const Content = styled.div`
  display: flex;
  width: 90%;
  justify-content: space-between;
  align-items: center;
  max-width: 1300px;
  margin: 0 auto;
  padding: 32px 0 32px 0;
  transform: translateY(1rem);
  /* transform: ${props =>
    props.scrollingUp ? `translateY(0)` : `translateY(1rem)`}; */
  /* transition: transform 0.5s; */
`

const SocialsContainer = styled.div`
  ul {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 32px;
    justify-items: center;
    grid-gap: 1.8rem;
    list-style: none;
    height: 2rem;
  }
  ul li {
    height: 2rem;
  }
  ul li img {
    width: auto;
    height: 2rem;
    &:hover {
      cursor: pointer;
    }
  }
  ul li button {
    all: unset;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    &:hover {
      cursor: pointer;
    }
  }
`

const Icon = styled.a`
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  img {
    width: 2.4rem;
    height: 2rem;
  }
`

export default function Nav() {
  const { isDark, changeTheme } = useContext(ThemeContext)
  return (
    <Navbar dark={isDark}>
      <Content>
        <img src={logo} alt="logo" height="auto" width="35px" />
        <SocialsContainer>
          <ul>
            <li>
              <Icon href="https://github.com/almirmujic">
                <img src={GitHub} alt="GitHub" />
              </Icon>
            </li>
            <li>
              <span>
                <Icon href="https://linkedin.com/in/almirmujic">
                  <img src={LinkedIn} alt="LinkedIn" />
                </Icon>
              </span>
            </li>
            <li>
              <span>
                <Icon href="mailto:contact.almir@gmail.com">
                  <img src={Email} alt="Email" />
                </Icon>
              </span>
            </li>
            <li>
              <button onClick={changeTheme}>
                <img src={isDark ? Sun : Moon} alt="Theme" />
              </button>
            </li>
          </ul>
        </SocialsContainer>
      </Content>
    </Navbar>
  )
}
